.marker-pin {
  width: 30px;
  height: 30px;
  border-radius: 50% 50% 50% 0;
  background: #f9849b;
  -webkit-box-shadow: 1px -1px 8px #777;
  box-shadow: 1px -1px 8px #777;
  -webkit-transform: translate(-15px, -37px) rotate(-45deg);
  -ms-transform: translate(-15px, -37px) rotate(-45deg);
  transform: translate(-15px, -37px) rotate(-45deg);
 }
 .marker-pin:after {
  content: '';
  width: 12px;
  height: 12px;
  margin: 9px 0 0 9px;
  background: #fff;
  position: absolute;
  border-radius: 60%;
 }


